<template>
    <div>
        <h3></h3>
        <hr />
        <el-card shadow="always">
            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.uid" ref="uid" size="mini" placeholder="" />
                </el-form-item>
                <el-form-item>
                <el-button type="primary" size="mini" @click="handleIdcheck"></el-button>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.upw" ref="upw" size="mini" type="password" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            
            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.userpw1" ref="userpw1" size="mini" type="password" placeholder=""></el-input>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.uage" ref="uage" size="mini" placeholder="" />
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <div class="demo-date-picker">
                        <div class="block">
                            <el-date-picker v-model="member.ubirth" ref="ubirth" size="mini" type="date" placeholder="">
                            </el-date-picker>
                        </div>
                    </div>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.email" size="mini" ref="email" placeholder="" />
                </el-form-item>
                <el-form-item>@</el-form-item>
                <el-form-item>
                        <el-select v-model="member.email1" ref="email1" size="mini" clearable placeholder="Select">
                            <el-option  v-for="tmp in emailoption" :key="tmp" :label="tmp" :value="tmp">
                            </el-option>
                        </el-select>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-radio-group v-model="member.ucheck" >
                        <el-radio v-for="tmp in radiooption" :key="tmp" :label="tmp"></el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-radio-group v-model="member.ugender">
                        <el-radio label="1"></el-radio>
                        <el-radio label="2"></el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>  

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label=""   label-width="80px">
                    <el-input type="textarea" v-model="member.text"></el-input>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label=""  label-width="80px">
                    <div>
                    <el-checkbox v-model="member.chk" label=""></el-checkbox>
                    </div>
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label=" " label-width="80px">
                    <el-button type="primary" @click="handleJoin"></el-button>
                    <el-button type="info" @click="handleHome"></el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    export default{
        data(){
            return{
                member: {
                    uid : '',  //
                    upw : '',  //
                    userpw1 : '',  //
                    uage : 1,    //
                    email : '',   //
                    email1 : '',    //
                    uemail : '',    //
                    ubirth : '',    //
                    ugender : '',
                    text : "",    
                    ucheck : '',     //
                   chk : false,
                   idcheck : false,
                },
                emailoption : ['naver.com','gmail.com','daum.net'],
                radiooption : ['1', '2','3','4',]
            }
        },
        methods:{
            async handleJoin(){
                if(this.member.uid === ""){
                    alert(' .');
                    this.$refs.uid.focus();
                    return false
                }
                if(this.member.upw === ""){
                    alert(' .');
                    this.$refs.upw.focus();
                    return false
                }
                if(this.member.userpw1 === ""){
                    alert(' .');
                    this.$refs.userpw1.focus();
                    return false
                }
                if(this.member.userpw1 !== this.member.upw){
                    alert('  .');
                    this.$refs.userpw1.focus();
                    return false
                }
                if(this.member.uage === ""){
                    alert(' .');
                    this.$refs.uage.focus();
                    return false
                }
                if(this.member.ubirth === ""){
                    alert(' .');
                    this.$refs.ubirth.focus();
                    return false
                }
                if(this.member.email === ""){
                    alert(' .');
                    this.$refs.email.focus();
                    return false
                }
                if(this.member.email1 === ""){
                    alert(' .');
                    this.$refs.email1.focus();
                    return false
                }
                if(this.member.ucheck === ""){
                    alert(' .');
                    return false
                }
                if(this.member.ugender === ""){
                    alert(' .');
                    return false
                }
                if(this.member.chk === false){
                    alert(' .');
                    return false
                }

                if(this.member.idcheck === false){
                    alert('  .');
                    return false
                }

                this.member.uemail = this.member.email + "@" + this.member.email1;

                //  
                const url = '/member/insert';
                const headers = {"Content-type":"application/json"};
                const body = this.member;
                const response = await this.axios.post(url, body, {headers:headers});
                console.log(response.data);

                if(response.data.status ===200){
                    alert(' ');
                    this.$router.push({name:'Home'});
                }

            },
            handleHome(){
                this.$router.push({name:'Home'});
                this.$emit('changeLogged', 'home');
            },

            async handleIdcheck(){
                //
                // if(this.member.uid === '' ){
                    
                // }

                if(this.member.uid.length <= 0){
                    alert(' .');
                    return false;
                }

                const url = `/member/idcheck?uid=${this.member.uid}`;
                const headers = {"Content-Type":"application/json"};
                const response = await this.axios.get(url, {headers:headers});
                console.log(response.data);
                if(response.data.result===1){
                    this.member.idcheck=false;
                    alert('    .');

                }
                else if(response.data.result===0){
                    this.member.idcheck=true;
                    alert('    .');
                }
            }

        }  
      
    };
    
</script>

<style scoped>

</style>