<template>
  <div>
    
    <el-container>
      <el-header>
        <el-menu  class="el-menu-demo" mode="horizontal" :router="true">
          <el-menu-item index="home" ref="home"></el-menu-item>
          <el-menu-item index="login" ref="login" v-show="!logged"></el-menu-item>
          <el-menu-item index="logout" ref="logout" v-show="logged"></el-menu-item>
          <el-menu-item index="mypage" ref="mypage" v-show="logged"></el-menu-item>
          <el-menu-item index="join" ref="join" v-show="!logged"></el-menu-item>
          <el-menu-item index="board" ref="board"></el-menu-item>
          <el-menu-item index="seller" ref="seller" v-show="logged"></el-menu-item>

        </el-menu>
      </el-header>
      <!-- v-if =    
         v-show =  ,  -->

      <el-main>
        <router-view @changeLogged="changeLogged"></router-view>
      </el-main>

      <el-footer>Footer</el-footer>
    </el-container>
    
  </div>
</template>

<script>
  import {useStore} from 'vuex';
  export default {
    //  .        x
    // DOM     .
    created(){

    },

    // DOM   ex)  ,  
    mounted(){
      //localhost:8080/abc / abc abc
      console.log(window.location.pathname.substr(1));
      const path = window.location.pathname.substr(1);
      this.changeLogged(path);
      //console.log(path);

      //     (, )
      //path    

      // store   
      this.store.subscribe((mutation, state)=>{
        console.log(mutation, state);
          // this.store.commit('setMenu', 'home');
        if(mutation.type==='setMenu'){
          const tmp = mutation.payload; //"home"
          this.changeLogged(tmp);
        }
      });
    },

    data(){
      return{
        logged : false,
        store : useStore()
      }
    },

    methods:{
      
      changeLogged(path){//   
        console.log('changeLogged', path);
        //   () 
        const token = sessionStorage.getItem("TOKEN");

        if(token === null){ // 
          this.logged = false; //state logged 
        }
        else{
          this.logged = true;
        }

        //this.$refs.home.click(); === this.$refs['home'].click();
        //this.$refs.login.click(); === this.$refs['login'].click();
        //this.$refs.path.click();   (x) === this.$refs[path].click(); //path  

        //  
        if(typeof this.$refs[path] !== 'undefined'){
        this.$refs[path].$el.click(); //path  
        }
      }
    } 
  }
</script>

