<template>
    <div>
        <h3>BoardOne</h3>
        <el-card shadow="always">
            <el-descriptions direction="vertical" :column="4" border>
            <el-descriptions-item label="" :span="2" >{{item._id}}</el-descriptions-item>
            <el-descriptions-item label="" :span="8">{{item.title}}</el-descriptions-item>
            <el-descriptions-item label="" :span="2" >{{item.writer}}</el-descriptions-item>
            <el-descriptions-item label="" :span="2" >{{item.content}}</el-descriptions-item>
        </el-descriptions>
                      
            <router-link to="/board">
                <el-button type="success" size="mini" plain> </el-button>
            </router-link>

            <el-button type="text" @click="dialog1 = true">
                <el-button type="primary"  size="mini" plain @click="hanldeDlete"></el-button>
            </el-button>

            <el-dialog v-model="dialog1" title="Tips" width="30%" :before-close="hanldeClose">
                <span>?</span>

                <template #footer>
                    <span class="dialog-footer">
                        <el-button type="primary" @click="hendelDel(no)"></el-button>
                        <el-button @click="dialog1 = false"></el-button>
                    </span>
                </template>
            </el-dialog>
            
            <el-button type="text" @click="dialog2 = true">
                <el-button type="primary" size="mini" plain @click="handleUp"></el-button>
            </el-button>

            <el-dialog v-model="dialog2" title="Tips" width="40%" :before-close="hanldeClose">
                <span></span>
                <hr />
                
                <p>{{item1._id}}</p>
                <el-input v-model="item1.title" placeholder="" />
                <el-input v-model="item1.content" :rows="10" type="textarea" placeholder="" />
                <template #footer>
                    <span class="dialog-footer">
                        <el-button type="primary" @click="hendeUpdate"></el-button>
                        <el-button @click="dialog2 = false"></el-button>
                    </span>
                </template>
            </el-dialog>
            <el-button type="primary" size="mini" plain @click="handlePrev"></el-button>
            <el-button type="primary" size="mini" plain @click="handleNext"></el-button>
        </el-card>
    </div>
</template>

<script>
    export default {
        created(){
            this.handleData();
           
            
   
        },

        data(){
            return{
                no : Number(this.$route.query.no),
                item : '',
                item1 : '',     
                dialog2 : false,
                dialog1 : false,
      
            }
        },
        methods:{
            handleUp(){
                this.dialog2=true;
            },
            hanldeDlete(){
                this.dialog1=true;
                
            },
            async hendelDel(no){
                const url = `/board/delete?&no=${no}`;
                const headers = {"Content-Type":"application/json"};
                const body = {};
                const response = await this.axios.delete(url, {headers:headers,data:body});
                console.log(response.data);
                if(response.data.status===200){
                alert('');
                this.$router.push({path:'/board'});
                }
            },

            async hendeUpdate(){
                const url = `/board/update`;
                const headers = {"Content-Type":"application/json"};
                const body = {
                    no      : this.item1._id,
                    title   : this.item1.title,
                    content : this.item1.content,
                 }
                const response = await this.axios.put(url, body, {headers:headers});
                if(response.data.status===200){
                    this.dialog2=false;
                    alert('');
                    this.handleData();
                }
            },

            async handleData(){
                const url = `/board/selectone?&no=${this.no}`;
                const headers = {"Content-Type":"application/json"};
                const response = await this.axios.get(url, {headers:headers});
                console.log(response.data);
                if(response.data.status===200){
                    // 
                    this.item = response.data.result;
                    //  
                    this.item1 = Object.create(this.item)
                }
            },

            async handlePrev(){
                const url = `/board/prevno?cno=${this.no}`;
                const headers = {"Content-Type":"application/json"};
                
                const response = await this.axios.get(url, {headers:headers});
                console.log('handlePrev');
                console.log(response.data);
                if(response.data.status===200){
                   if(response.data.no <= 0){
                       alert(' ');
                   }
                   else{
                       this.no = response.data.no;

                       // ->  push 
                       //  created, mounted .
                       //replace  
                       this.$router.push({
                           name : 'BoardOne',
                           query : {bno : 1, no : this.no}
                       });
                       
                       this.handleData();
                       this.handleHit();
                   }
                }
            },

            async handleNext(){
                const url = `/board/nextvno?cno=${this.no}`;
                const headers = {"Content-Type":"application/json"};
                const response = await this.axios.get(url, {headers:headers});
                if(response.data.status===200){
                   if(response.data.no <= 0){
                       alert(' ');
                   }
                   else{
                       this.no=response.data.no;
                       this.handleData();
                       this.handleHit();
                   }
                }
            },

            async handleHit(){
                const url = `/board/updatehit?no=${this.no}`;
                const headers = {"Content-Type":"application/json"};
                const response = await this.axios.put(url,{}, {headers:headers});
                if(response.data.status===200){
                    console.log(' ');
                }
            }
        }
        
    }
</script>

<style scoped>
