    float: left;
    background-color: #95b5f0;
    width: 10%;
    height: 500px;
    padding: 10px;
}

footer {
    background-color: #e96565;
    text-align: center;
    padding: 20px;
}

section::after{
    content: "";
    clear: both;
    display: block;
}

ul {
    list-style-type: none;
}

a{
    text-decoration: none;
}

table{
    width: 100%;
    border-collapse: collapse;
}

tbody{
    text-align: center;
}



 .mydiv {
    border:10px solid #ee4a4a; /*  */
    margin-bottom: 20px;        /*   */
    padding : 10px;             /* (4)  */
}

.container{
    width: 800px;
    padding: 30px;
    border: 2px solid #796767;
}

.lbl1{
    width: 70px;
    display: inline-block;
}

.mydiv1{
    margin-bottom: 5px;
}

.mydiv2 {
    width: 500px;
    margin-bottom: 5px;
    border: 1px solid #cccccc;
}

.container2{
    width: 800px;
    border: 3px;
  }
