<template>
    <div>
        <h3></h3>
        <hr />
        <el-card shadow="always">
            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.uid" ref="uid" size="mini" placeholder="" />
                </el-form-item>
            </el-form>

            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label="" label-width="80px">
                    <el-input v-model="member.upw" ref="upw" size="mini" type="password" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <el-form :inline="true" class="demo-form-inline" style="margin-bottom:-20px">
                <el-form-item label=" " label-width="80px">
                    <el-button type="primary" size="mini" @click="handleLogin"></el-button>
                    <el-button type="info" size="mini" @click="handleJoin"></el-button>
                </el-form-item>
            </el-form>
        </el-card>
        
    </div>
</template>

<script>
    import {useStore} from 'vuex';
    export default {
        methods:{
            async handleLogin(){
                if(this.member.uid === ""){
                    alert(' .');
                    this.$refs.uid.focus();
                    return false
                }
                 if(this.member.upw === ""){
                    alert(' .');
                    this.$refs.upw.focus();
                    return false
                }

                const url =`/member/select`;
                const headers = {"Content-Type":"application/json"};
                const body = this.member;
                const response = await this.axios.post(url, body, {headers:headers}) 
                console.log(response.data);
                if(response.data.status===200){
                    this.$router.push({name:'Join'});
                    //1.   
                    sessionStorage.setItem("TOKEN", response.data.result.token);
                    alert(' .');
                    //2.  
                    this.$router.push({name:'Home'});

                    //3. App.vue   .
                    this.store.commit('setMenu', 'home');
                }
                else{
                    alert('   .');
                }

                
                //   
                // ( changeLogged)
                //this.$emit('changeLogged', 'home');
            },
            handleJoin(){
                // App.vue   .
                // App.vue   .
                this.$router.push({name:'Join'});
                this.store.commit('setMenu', 'join');
            }
        },
        data(){
            return{
  
                member:{
                    uid :'',
                    upw :'',
                    token : '',
                },
                store : useStore()
            }
        },
    }
</script>

