<template>
    <div>
        <el-button type="primary" @click="handleWrite"></el-button>
        <el-table :data="items" style="width: 100%; cursor:pointer;" @row-click="rowclick">
            <el-table-column prop="_id" label="" width="120" />
            <el-table-column prop="title" label="" width="300" />
            <el-table-column prop="writer" label="" width="250" />
            <el-table-column prop="hit" label="" width="200" />
            <el-table-column prop="regdata" label="" width="250" />
        </el-table>

        <el-pagination layout="prev, pager, next" :total="total" @current-change="currentchange">
        </el-pagination>
    </div>
</template>

<script>
    import {useStore} from 'vuex';
    export default {
        //state (   )
        data(){
            return{
                items : [],
                store : useStore(),
                page : 1,
                total : 0,
            }
        },
        created(){
            this.handleData();
        },

        mounted(){
            //      
            // this.store.subscribe((mutation, state) => {
            //     console.log(mutation, state);
            //     // items    .
            //     if(mutation.type === 'setItems'){
            //         this.items = mutation.payload;
            //     }
            // });
        },
        methods:{
            handleWrite(){
                this.$router.push({ name : 'BoardWrite' })
            },
            //    items 
            async handleData(){
                const url ="/board/select"
                const header ={"Content-Type":"application/json"};

                const response = await this.axios.get(
                    url,
                    {params :{page:this.page}},
                    {header:header}
                );
                console.log(response.data);
                if(response.data.status===200);
                    this.items = response.data.result;
                    this.total = response.data.total;

            //     // action   handleData
            //     await this.store.dispatch('handleData', {page:this.page});

                // store mutation (   -)
                // this.store.commit('setCounter', 30);

                // store getters 
                // const count = this.store.getters.getCounter;
                // this.items = this.store.getters.getItems;

            },
            //    
            currentchange(page){
                console.log('board => currentchange', page);
                this.page = page;
                this.handleData();
            },
            async rowclick(row){
                console.log('board => rowclick',row);
                // 1 
                const url =`/board/updatehit?no=${row._id}`
                const header ={"Content-Type":"application/json"};
                const response = await this.axios.put(
                    url,
                    {},
                    {header:header}
                );

                console.log(response.data);
                if(response.data.status===200){
                    this.$router.push({
                        name : 'BoardOne',
                        query : {bno : 1, no : row._id}
                    })
                }
            }
        }
      
        
    }
</script>

