//CMD> npm i vuex@next --save
import { createStore } from "vuex";
import axios from 'axios';

export default createStore({

    // ()
    //    
    state : {
        counter : 20,
        menu    : 'home',
        items   : [], 
    },

    //     
    getters : {
        getItems(state){
            return state.items;
        },

        getMenu(state){
            return state.menu;
            
        },
        getCounter(state) { //  
            return state.counter;
        },

        getCounter1: state => { //  
            return state.counter * 2;
        }
    },
    //   ( :  )
    mutations : {
        setItems(state, value){
            state.items = value;
        },

        setMenu : (state, value) => {
            state.counter =value;
        },

        setCounter : (state, value) => {
            state.counter =value;
        },

        setCounter1(state, value){
            state.counter = value + 100;
        }
    },

    //   ( :     X)
    actions : {
       
        //    page 
        async handleData(context, payload){
            const page = payload.page;
            console.log(page);

            const url = `http://ihongss.com/json/board.json?page=${page}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url, {headers:headers});
            if(response.data.ret === 'y'){
                context.commit('setItems', response.data.data);
            }
        }
    },

})