<template>
    <div>
        <h3></h3>
        <el-descriptions direction="vertical" :column="4" border>
            <el-descriptions-item label="" :span="2">{{item._id}}</el-descriptions-item>
            <el-descriptions-item label="" :span="2">{{item.name}}</el-descriptions-item>
            <el-descriptions-item label="" :span="2">{{item.price}}</el-descriptions-item>
            <el-descriptions-item label="" :span="2" >{{item.quantity}}</el-descriptions-item>
            <el-descriptions-item label="" :span="2"><el-image :src="item.image" style="width:50%"/>
            </el-descriptions-item>
        </el-descriptions>
        
        <el-button type="primary" @click="handleUpdate"></el-button>
        <el-button type="primary" @click="handleSeller"></el-button>
    </div>
</template>

<script>
    export default {
        created(){
            this.handleData();
   
        },

        data(){
            return{
                item : '',
                code : this.$route.query.code, //  
                
            }
        },
        methods:{
            async handleData(){
                const url = `/item/selectone?code=${this.code}`;
                const headers = {"Content-Type":"application/json"};

                const response = await this.axios.get(url, {headers:headers});
                if(response.data.status===200){
                    this.item = response.data.result;
                    console.log(this.item);
                }
            },
            handleSeller(){
            this.$router.push({name:'Seller'}); 
            }
        }
        
        
    }
</script>

